<template>
  <div
    id="delProductModal"
    ref="delProductModal"
    class="modal fade"
    tabindex="-1"
    aria-labelledby="delProductModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content border-0">
        <div class="modal-header bg-danger text-white">
          <h5 id="delProductModalLabel" class="modal-title">
            <span>刪除</span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          是否刪除
          <strong class="text-danger">{{ delProduct.title }}</strong> 商品(刪除後將無法恢復)。
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
            取消
          </button>
          <button type="button" class="btn btn-danger" @click="deleteProduct">確認刪除</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal';

export default {
  props: {
    delProduct: {
      type: Object,
      default() {
        return {};
      },
    },
    currentPage: {
      // currentPage = pagination.current_page 把當前頁面帶進來
      type: Number,
      default: 1,
    },
  },
  data() {
    return {
      delProductModal: '',
    };
  },
  methods: {
    deleteProduct() {
      const status = '刪除商品';
      const { id } = this.delProduct;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`;
      this.$http
        .delete(url)
        .then((res) => {
          this.$messageState(res, status);
          this.$emit('update-product', this.currentPage);
          this.closeDeleteProductModal();
        })
        .catch((err) => {
          this.$messageState(err.response, '錯誤訊息');
        });
    },
    openDeleteProductModal() {
      // 開啟刪除產品 modal
      this.delProductModal.show();
    },
    closeDeleteProductModal() {
      // 關閉刪除產品 modal
      this.delProductModal.hide();
    },
  },
  mounted() {
    this.delProductModal = new Modal(this.$refs.delProductModal);
  },
};
</script>
